.component-analyze .numbers {
  display: flex;
  justify-content: space-around;
  padding: 0;
}
.component-analyze .numbers li.number-item {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-list .case-list .am-list-body {
  background: transparent;
}
.page-list .list-item {
  margin-bottom: 16px;
  padding: 16px;
  background-color: white;
}

.app-tabs {
  display: flex;
  padding: 0;
}
.app-tabs li.app-tab-pane {
  list-style: none;
  padding: 0;
  margin: 0 16px 0 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

